.hW {
  overflow: hidden;
  animation: type 3.5s steps(40, end),
  blink-caret .75s step-end infinite;
  font-size: 70px;
}

@keyframes type{
  from { width: 0; }
}



/******************* Lesson ONE ************************/

#work.container {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 85%;
  height: auto;
  /* background: #f1f1f1; */
  margin: 40px auto 40px;
  word-break: break-all;
  border: 1px solid rgb(0, 0, 0);

}


@media screen and (max-width:479px) {
  
    #work.container {
      font-size: 10px ;
    }
  }

@media screen and (max-width:979px) {
  

  .textArea {
    position: relative;
    right: 100px;
  }
  .submit {
    position: relative;
    right: 100px;
    margin-top: 5%;
  }
 
}

.bloc-tabs {
  display: flex;
}

.tabs {
  padding: 1%;
  text-align: center;
  width: 30%;
  background: rgba(128, 128, 128, 0.075);
  cursor: pointer;
  border-bottom: 1px solid rgb(255, 0, 0);
  box-sizing: content-box;
  position: relative;
  outline: none;
  color: rgb(65, 57, 57);
  
}
.tabs:not(:last-child){
  border-right: 1px solid rgba(0, 0, 0, 0.274);
}

.active-tabs  {
  background: white;
  border-bottom: 1px solid transparent;
  color: black;
}

.active-tabs::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% + 2px);
  height: 5px;
  background: rgb(88, 147, 241);
  
}


button {
  border: none;
  
}
.content-tabs {
  flex-grow : 1;
  background-color: rgba(3, 27, 241, 0.767);
}
.content {
  /* background: rgb(0, 0, 0); */
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 10px;
  width: 80%;
  height: auto;
  display: none;
  white-space: pre;
  color: gray;
}

.content h2 {
  padding: 0px 0 5px 0px;
  font-size: 25px;
}
.content hr {
  width: 100px;
  height: 2px;
  background: #222;
  margin-bottom: 15px;
}
.content p {
  width: 80%;
  height: 100%;
  position: relative;
  
}
.active-content {
  display: block;
}




